A;;AD2lB7E;ECvlBI,0DAAyD,EAAA;;ADulB7D;ECnlBI,0DAA4D,EAAA;;ADmlBhE;EC/kBI,0DAAoD,EAAA;;AD+kBxD;EC3kBI,yDAA6D,EAAA;;AD2kBjE;ECrkBI,yDAAuD,EAAA;;ADqkB3D;ECjkBI,0DAA8D,EAAA;;ADikBlE;EC3jBI,0DAAqD,EAAA;;AD2jBzD;ECrjBI,sBAAsB;EACtB,2BAA2B,EAAA;;ADojB/B;EC9iBI,+BAA+B,EAAA;;AD8iBnC;EC1iBI,+BAA+B,EAAA;;ADskBnC;EACI,+BAA+B;EAC/B,yBAAiC;EACjC,gBAAgB,EAAA;EAHpB;IAMQ,aAAa;IACb,2BAA2B;IAC3B,mBAAmB,EAAA;IAR3B;MAWY,YAAY;MACZ,eAAe;MACf,eAAe,EAAA;EAb3B;IAmBY,WAAW;IACX,mBAAmB,EAAA;EApB/B;IAwBY,aAAa;IACb,2BAA2B,EAAA;IAzBvC;MA4BgB,aAAa;MACb,sBAAsB;MACtB,UAAU,EAAA;MA9B1B;QAiCoB,eAAe,EAAA;EAjCnC;IAwCQ,WAAW;IACX,iBAAiB,EAAA;EAzCzB;IA6CQ,2BAA2B;IAC3B,gBAAgB,EAAA;EA9CxB;IAkDQ,gBAAgB;IAChB,mBAAmB,EAAA;;AAI3B;EACI;IACI,eAAe,EAAA,EAClB;;AAGL;EACI;IACI,eAAe,EAAA;EAGnB;IACI,kBAAkB,EAAA,EACrB","sourcesContent":[".add-to-wishlist {\r\n    display: none;\r\n    height: 20px;\r\n    aspect-ratio: 1;\r\n    box-shadow: 0px 0px 2px 4px rgba(2, 2, 2, 0.274);\r\n    border-radius: 100vh;\r\n    cursor: pointer;\r\n    margin-left: auto;\r\n    margin-top: 4px;\r\n    margin-right: 4px;\r\n    transition: all 1s;\r\n    background-color: rgb(0, 0, 0);\r\n\r\n    @media screen and(max-width:600px) {\r\n        display: block;\r\n    }\r\n\r\n    :hover {\r\n        transform: scale(110%);\r\n    }\r\n\r\n    svg {\r\n        fill: rgb(238, 238, 238);\r\n    }\r\n}\r\n\r\n.warn-box {\r\n    position: fixed;\r\n    top: 1rem;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    background-color: rgb(51, 51, 51);\r\n    padding: 2rem;\r\n    border-radius: 1rem;\r\n    overflow: hidden;\r\n\r\n    p {\r\n        position: relative;\r\n        z-index: 3;\r\n    }\r\n}\r\n\r\n.warn-box::after {\r\n    content: \"\";\r\n    z-index: 2;\r\n    top: 0;\r\n    left: 0;\r\n    position: absolute;\r\n    height: 100%;\r\n    width: 100%;\r\n    background-color: rgb(68, 68, 68);\r\n    animation: cooldown 4s linear;\r\n}\r\n\r\n@keyframes cooldown {\r\n    0% {\r\n        width: 100%;\r\n    }\r\n\r\n    100% {\r\n        width: 0%;\r\n    }\r\n}\r\n\r\n.image-bg-div {\r\n    width: 100%;\r\n    background-color: rgb(73, 73, 73);\r\n    border-radius: .5rem;\r\n    overflow: hidden;\r\n    background-size: cover;\r\n    background-position: center;\r\n}\r\n\r\nmain {\r\n\r\n    position: relative;\r\n    padding-inline: 7%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 3rem;\r\n\r\n    @media screen and(max-width: 1200px) {\r\n        padding-inline: 1rem;\r\n    }\r\n\r\n    @media screen and(max-width: 720px) {\r\n        gap: 1rem;\r\n    }\r\n\r\n    .toggled-menu {\r\n        position: absolute;\r\n        top: 1rem;\r\n        left: 1rem;\r\n        z-index: 1;\r\n\r\n        display: none;\r\n        grid-template-areas: \"play create\"\r\n            \"discover create\";\r\n        padding: 2rem;\r\n        gap: 3.5rem;\r\n\r\n        gap: clamp(1rem, 5vw, 3.5rem);\r\n\r\n        background-color: hsl(240, 6%, 13%);\r\n        border-radius: 1.5vh;\r\n\r\n        >div>h2 {\r\n            margin-bottom: 1rem;\r\n            font-size: 1.2rem;\r\n            font-weight: 600;\r\n        }\r\n\r\n        >div>ul {\r\n            display: flex;\r\n            flex-direction: column;\r\n            gap: .5rem;\r\n\r\n            color: rgba(255, 255, 255, 0.473);\r\n            font-size: .9rem;\r\n\r\n            li {\r\n                border-radius: .5rem;\r\n                padding: .5rem 1rem;\r\n                cursor: pointer;\r\n            }\r\n\r\n            li:hover {\r\n                background-color: hsl(240, 3%, 26%);\r\n                color: white;\r\n            }\r\n        }\r\n\r\n        .play {\r\n            grid-area: play;\r\n        }\r\n\r\n        .discover {\r\n            grid-area: discover;\r\n        }\r\n\r\n        .create {\r\n            grid-area: create;\r\n        }\r\n\r\n    }\r\n\r\n    .toggled-menu.active {\r\n        display: grid;\r\n    }\r\n\r\n    .search-container {\r\n        max-width: 1430px;\r\n        width: 80%;\r\n        margin-inline: auto;\r\n        height: 60px;\r\n        align-items: center;\r\n        gap: 1.5rem;\r\n        margin-top: 3rem;\r\n\r\n        @media screen and(max-width: 720px) {\r\n            margin-top: 1rem;\r\n        }\r\n\r\n        .search-bar {\r\n            background-color: hsl(0, 0%, 13%);\r\n            border-radius: 100vw;\r\n            height: 40px;\r\n            max-width: 230px;\r\n            padding: .5rem;\r\n            gap: .5rem;\r\n\r\n            span {\r\n                font-size: 20px;\r\n            }\r\n\r\n            input {\r\n                background-color: transparent;\r\n                outline: none;\r\n                border: none;\r\n                color: white;\r\n                font-family: poppins;\r\n            }\r\n        }\r\n\r\n        .discover-menu {\r\n            gap: 1.5rem;\r\n\r\n            @media screen and(max-width: 720px) {\r\n                display: none;\r\n            }\r\n        }\r\n\r\n        .cart {\r\n            margin-left: auto;\r\n\r\n            >span {\r\n                font-size: 25px;\r\n            }\r\n        }\r\n    }\r\n\r\n    .carousel-container {\r\n        max-width: 1430px;\r\n        margin-inline: auto;\r\n        display: grid;\r\n        grid-template-columns: 8fr 2fr;\r\n        gap: 1rem;\r\n        height: 35em;\r\n\r\n        @media screen and(max-width: 1000px) {\r\n            grid-template-columns: 1fr;\r\n        }\r\n\r\n        .carousel {\r\n            position: relative;\r\n            overflow: hidden;\r\n            border-radius: 1.5rem;\r\n\r\n            .carousel-inner {\r\n                display: flex;\r\n                height: 100%;\r\n\r\n                .carousel-item-1 {\r\n                    background: url(../imgs/carousel/controll.jpg);\r\n                }\r\n\r\n                .carousel-item-2 {\r\n                    background: url(../imgs/carousel/genshin-impact.jpg);\r\n                }\r\n\r\n                .carousel-item-3 {\r\n                    background: url(../imgs/carousel/watch-dogs2.jpg);\r\n                }\r\n\r\n                [class*=\"carousel-item\"] {\r\n                    width: 100%;\r\n                    height: 100%;\r\n                    background-size: cover;\r\n                    background-position: center 0%;\r\n                    backdrop-filter: brightness(0.8);\r\n                    transform: translateX(20%);\r\n                    transition: transform 0.5s ease;\r\n\r\n                    display: none;\r\n                    flex-direction: column;\r\n                    align-items: start;\r\n                    justify-content: end;\r\n                    padding: 7%;\r\n\r\n                    >:first-child {\r\n                        opacity: .7;\r\n                        margin-bottom: 2rem;\r\n                    }\r\n\r\n                    >:nth-child(2) {\r\n                        font-weight: 600;\r\n                        margin-bottom: .7rem;\r\n                        opacity: .9;\r\n                    }\r\n\r\n                    >:nth-child(3) {\r\n                        font-size: 1rem;\r\n                        margin-bottom: 2rem;\r\n                    }\r\n\r\n                    .purchase-div {\r\n                        align-items: center;\r\n                        gap: 1rem;\r\n\r\n                        .add-to-wishlist {\r\n                            align-items: center;\r\n                            gap: .5rem;\r\n                            cursor: pointer;\r\n\r\n                            >span {\r\n                                font-size: 1rem;\r\n                            }\r\n\r\n                            >p {\r\n                                font-size: .8rem;\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n\r\n                [class*=\"carousel-item\"].active {\r\n                    display: flex;\r\n                    transform: translateX(0%);\r\n                    animation: slidein 0.3s ease forwards;\r\n                }\r\n\r\n                [class*=\"carousel-item\"].hide {\r\n                    animation: slideout 0.2s ease forwards;\r\n                    animation-delay: 3.9s;\r\n                }\r\n\r\n                @keyframes slidein {\r\n                    from {\r\n                        transform: translateX(-50%);\r\n                    }\r\n\r\n                    to {\r\n                        transform: translateX(0%);\r\n                    }\r\n                }\r\n\r\n                @keyframes slideout {\r\n                    from {\r\n                        transform: translateX(0%);\r\n                    }\r\n\r\n                    to {\r\n                        transform: translateX(-50%);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        .items-container {\r\n            display: grid;\r\n            // font-size: 1rem;\r\n            gap: .3rem;\r\n            grid-template-rows: repeat(5, 1fr);\r\n\r\n            @media screen and(max-width: 1000px) {\r\n                display: none;\r\n            }\r\n\r\n\r\n            [class*=\"item-box\"] {\r\n                display: grid;\r\n                grid-template-columns: 2fr 5fr;\r\n                align-items: center;\r\n                gap: 1rem;\r\n                padding: 1rem;\r\n                cursor: pointer;\r\n                border-radius: 1rem;\r\n                position: relative;\r\n                overflow: hidden;\r\n\r\n                .fill-background {\r\n                    position: absolute;\r\n                    left: 0;\r\n                    z-index: 0;\r\n                    background: hsla(0, 0%, 48%, 0.452);\r\n                    height: 100%;\r\n                    width: 0%;\r\n                }\r\n\r\n                .fill-background.active {\r\n                    animation: fillAnimation ease-out 4s;\r\n                }\r\n\r\n                @keyframes fillAnimation {\r\n                    0% {\r\n                        width: 0;\r\n                    }\r\n\r\n                    100% {\r\n                        width: 100%;\r\n                    }\r\n                }\r\n\r\n                [class*='item-image'] {\r\n                    background-color: hsl(0, 0%, 15%);\r\n                    height: 100%;\r\n                    width: auto;\r\n                    border-radius: .5rem;\r\n                    background-size: cover;\r\n                    position: relative;\r\n                    z-index: 1;\r\n                }\r\n\r\n                .item-name {\r\n                    position: relative;\r\n                    z-index: 1;\r\n                }\r\n            }\r\n\r\n            [class*=\"item-box\"]:hover {\r\n                background-color: hsl(0, 0%, 16%);\r\n            }\r\n        }\r\n    }\r\n\r\n    @media screen and (max-width: 1150px) {\r\n        .search-container {\r\n            width: 100%;\r\n            // padding: 1rem;\r\n        }\r\n\r\n        .carousel-container {\r\n            width: 100%;\r\n            // padding: 1rem\r\n        }\r\n    }\r\n}\r\n\r\n// section scroll cards\r\n\r\n.card-section-root {\r\n    .section-top {\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n        margin-bottom: 1rem;\r\n\r\n        .section-title {\r\n            font-size: 1.2rem;\r\n            display: flex;\r\n            align-items: center;\r\n        }\r\n\r\n        .buttons {\r\n            display: flex;\r\n            gap: 1rem;\r\n\r\n            >div {\r\n                height: 2.5rem;\r\n                aspect-ratio: 1;\r\n                cursor: pointer;\r\n\r\n                svg {\r\n                    fill: white;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .item-view-slide-container {\r\n        width: 100%;\r\n        overflow: scroll;\r\n        transition: scroll 2s ease-in-out;\r\n\r\n        .items-container {\r\n            width: fit-content;\r\n            display: flex;\r\n            transition: all 2s;\r\n\r\n            .card {\r\n                // background-color: green;\r\n                border-radius: .5rem;\r\n                overflow: hidden;\r\n                margin-inline: 10px;\r\n                cursor: pointer;\r\n\r\n                .image-bg-div {\r\n                    width: 100%;\r\n                    aspect-ratio: 5 / 6;\r\n                    background-color: rgb(73, 73, 73);\r\n                    border-radius: .5rem;\r\n                    overflow: hidden;\r\n                    background-size: cover;\r\n                    background-position: center;\r\n                    transition: background-size 1s ease;\r\n                    background-repeat: no-repeat;\r\n                    position: relative;\r\n                }\r\n\r\n                .image-bg-div:hover::after {\r\n                    content: \"\";\r\n                    position: absolute;\r\n                    top: 0px;\r\n                    left: 0px;\r\n                    height: 100%;\r\n                    width: 100%;\r\n                    background-color: rgba(255, 255, 255, 0.178);\r\n                    pointer-events: none;\r\n                }\r\n\r\n                .content {\r\n                    padding: 1rem 0rem;\r\n                    display: flex;\r\n                    flex-direction: column;\r\n                    gap: .5rem;\r\n\r\n                    small {\r\n                        text-transform: uppercase;\r\n                        opacity: .7;\r\n                        font-size: .7rem;\r\n                    }\r\n\r\n                    .pricing {\r\n                        display: flex;\r\n                        gap: clamp(0.1rem, 1vw, 1rem);\r\n                        align-items: center;\r\n\r\n                